import React, { PureComponent } from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { EmbedPlugin, EmbedNode } from '@ali/4ever-bamboo';
import { AudioPluginConfig } from '../types';

const {
  MobileEmbedCard, AudioPreview,
} = EmbedPlugin;

interface IProps {
  controller: Controller;
  attributes: any;
  node: EmbedNode;
  locale: any;
  mobile: boolean;
  isSelected: boolean;
  transformAudioURL?: (src: string) => string;
  onMediaLoadSuccess?: any;
  onMediaLoadError?: any;
  scrollableContainer: HTMLElement | Window;
  onDownload?: any;
  allowDownload?: ((src: string) => Promise<boolean>) | boolean;
  onPreview?: AudioPluginConfig['onPreview'];
}

interface IState {
  allowDownload: boolean;
}

class Audio extends PureComponent<IProps, IState> {
  ref = React.createRef();

  previewRef = React.createRef();

  constructor(props: IProps) {
    super(props);
    const { allowDownload } = props;
    this.state = {
      allowDownload: typeof allowDownload === 'boolean' ? allowDownload : false,
    };
  }

  componentDidMount() {
    const { allowDownload } = this.props;
    const { src } = this.getNodeData();

    if (typeof allowDownload === 'function') {
      allowDownload(src).then(res => {
        this.setState({
          allowDownload: res,
        });
      });
    }
  }

  getNodeData = () => {
    const { node } = this.props;
    const { data } = node || {};
    return data;
  };

  onMediaLoadError = () => {
    const { src } = this.getNodeData();
    const { onMediaLoadError } = this.props;
    if (onMediaLoadError) {
      onMediaLoadError(src);
    }
  };

  onMediaLoadSuccess = () => {
    const { src } = this.getNodeData();
    const { onMediaLoadSuccess } = this.props;
    if (onMediaLoadSuccess) {
      onMediaLoadSuccess(src);
    }
  };

  onDownloadFile = () => {
    const { src, name, type, size } = this.getNodeData();
    const { onDownload } = this.props;
    if (onDownload) {
      onDownload(src, {
        name,
        type,
        size,
      });
    }
  };

  renderPreview() {
    const { allowDownload } = this.state;
    const { transformAudioURL, locale } = this.props;

    const { name, size, src } = this.getNodeData();

    return (
      <AudioPreview
        allowDownload={allowDownload}
        onMediaLoadSuccess={this.onMediaLoadSuccess}
        onMediaLoadError={this.onMediaLoadError}
        transformAudioURL={transformAudioURL}
        locale={locale}
        name={name}
        size={size}
        src={src}
        onDownloadFile={this.onDownloadFile}
        isMobile
      />
    );
  }

  renderCard() {
    const { name, src, type, size } = this.getNodeData();
    const { onPreview } = this.props;

    return (
      <MobileEmbedCard
        name={name}
        onPreview={onPreview}
        src={src}
        type={type}
        size={size}
      />
    );
  }

  render() {
    const { viewType } = this.getNodeData();
    const { attributes } = this.props;

    return (
      <React.Fragment>
        <div {...attributes} ref={this.ref}>
          {viewType === 'preview' ? this.renderPreview() : this.renderCard()}
        </div>
      </React.Fragment>
    );
  }
}

export default Audio;
